var storage = require('./storage');
var login = require('./login');
//
module.exports = function (app) {
  app.then(() => storage.getSession() //NE PASSE JAMAIS PAR LA 
    .then((session) => {
      if (session) {
        app.session = session;
        app.connect();
      }
    }));
  return {
    login: () => {
      var m = new login.Model();
      m.on('login', (ok, info) => {
        if (ok) {
          app.session = info;
          app.connect();
        }
      });
      app.showView(new login.View({ //NI PAR LA
        model: m
      }));
    }
  };
};



